/*! minireset.css v0.0.6 | MIT License | github.com/jgthms/minireset.css */
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
   margin: 0;
   padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
   font-size: 100%;
   font-weight: normal;
}
ul {
   list-style: none;
}
button,
input,
select {
   margin: 0;
}
html {
   box-sizing: border-box;
}
*,
*::before,
*::after {
   box-sizing: inherit;
}
img,
video {
   height: auto;
   max-width: 100%;
}
iframe {
   border: 0;
}
table {
   border-collapse: collapse;
   border-spacing: 0;
}
td,
th {
   padding: 0;
}
@keyframes heartgrow {
   0% {
      transform: scale(0.4);
      opacity: 0.1;
   }
   50% {
      transform: scale(0.6);
      opacity: 0.3;
   }
   80% {
      transform: scale(1);
      opacity: 0.5;
   }
   100% {
      transform: scale(1.2);
      opacity: 0.7;
   }
}
@keyframes heartgrow2 {
   0% {
      transform: scale(1, 0);
   }
   25% {
      transform: scale(1, 1.1);
   }
   50% {
      transform: scale(1, 1.2);
   }
   75% {
      transform: scale(1, 1.1);
   }
   100% {
      transform: scale(1, 1);
   }
}
@keyframes fadein {
   from {
      opacity: 0;
      transform: translateY(15px);
   }
   to {
      opacity: 1;
      transform: translateY(0);
   }
}
@keyframes loader {
   0% {
      transform: rotate(0deg);
   }
   25% {
      transform: rotate(-45deg) scale(0.8);
   }
   50% {
      transform: rotate(405deg) scale(1.3);
   }
   100% {
      transform: rotate(360deg);
   }
}
@keyframes loader-opacity {
   0% {
      opacity: 1;
   }
   100% {
      opacity: 0;
      z-index: -1;
   }
}
.restaurant-page-wrapper {
   background-color: #1d2021;
   border-radius: 40px 40px 0 0;
   margin-top: -65px;
   padding: 30px 0;
   position: relative;
}
@media screen and (min-width: 1366px) {
   .restaurant-page-wrapper {
      width: 80%;
      margin-left: auto;
      margin-right: auto;
   }
}
.restaurant-page-wrapper .restaurant-page {
   width: 90%;
   margin: auto;
}
@media screen and (min-width: 1366px) {
   .restaurant-page-wrapper .restaurant-page__menus-wrapper {
      width: 30%;
   }
}
@media screen and (min-width: 1366px) {
   .restaurant-page-wrapper .restaurant-page__flex {
      display: flex;
      justify-content: space-between;
   }
}
.restaurant-page-wrapper .restaurant-page__label {
