}
.restaurant-page-wrapper .restaurant-page__restaurant-name i {
   margin: auto;
   color: #7d7d7d;
   transform: scale(1.4, 1.7);
   position: absolute;
   right: 20px;
   top: 27%;
}
.restaurant-page-wrapper .restaurant-page__label-wrapper {
   margin-bottom: 20px;
}
.restaurant-page-wrapper .restaurant-page__label-wrapper span::before {
   content: ' ';
   height: 3px;
