}
.restaurant-page-wrapper .restaurant-page__plate {
   background-color: #181a1b;
   border-radius: 15px;
   display: flex;
   overflow: hidden;
   width: 100%;
   box-shadow: 1px 6px 6px rgba(50, 50, 50, 0.1);
   margin-bottom: 15px;
   transition: 0.5s;
   color: #fff;
}
@media screen and (min-width: 768px) {
   .restaurant-page-wrapper .restaurant-page__plate {
      width: 80%;
      margin-left: auto;
      margin-right: auto;
   }
}
@media screen and (min-width: 1366px) {
   .restaurant-page-wrapper .restaurant-page__plate {
      margin-bottom: 30px;
      width: 100%;
   }
}
.restaurant-page-wrapper .restaurant-page__plate-description {
   width: 100%;
   display: flex;
   max-width: 100%;
   margin-bottom: 9px;
   overflow: hidden;
}
.restaurant-page-wrapper .restaurant-page__plate-description-text {
   width: 100%;
