}
@media screen and (min-width: 768px) {
   .restaurant-page-wrapper .restaurant-page__plate-description-text {
      margin-left: 20px;
   }
}
.restaurant-page-wrapper .restaurant-page__plate-description-text__plate-name {
   margin: 8px 0 0 0;
   font-weight: bold;
   font-size: 1.1em;
   line-height: 1.5em;
   text-overflow: ellipsis;
   white-space: nowrap;
   overflow: hidden;
}
@media screen and (min-width: 768px) {
   .restaurant-page-wrapper
