.restaurant-page-wrapper .restaurant-page__plate-price-wrapper {
   display: flex;
   align-items: flex-end;
   margin-bottom: 3px;
   margin-right: 9px;
   font-size: 0.9em;
}
@media screen and (min-width: 768px) {
   .restaurant-page-wrapper .restaurant-page__plate-price-wrapper {
      margin-bottom: 9px;
   }
}
.restaurant-page-wrapper .restaurant-page__plate-price {
   font-weight: bold;
   margin-right: 10px;
}
.restaurant-page-wrapper .restaurant-page__check-wrapper {
   width: 30%;
   background-color: #1d6558;
   display: flex;
   justify-content: center;
   align-items: center;
   margin-right: -30%;
   transition: 0.5s ease;
}
.restaurant-page-wrapper .restaurant-page__check-wrapper i {
   color: #1d6558;
   padding: 5px;
   border-radius: 50%;
   background: #fff;
   transition-duration: 0.5s;
}
.restaurant-page-wrapper .restaurant-page__plate:hover {
   cursor: pointer;
}
.restaurant-page-wrapper
   .restaurant-page__plate:hover
   .restaurant-page__check-wrapper {
   margin-right: 0;
   transition: 0.5s ease;
}
.restaurant-page-wrapper .restaurant-page__plate:hover .fa-check {
   transform: rotate(360deg);
   transition: 0.5s;
}
.restaurant-page-wrapper .restaurant-page__button-wrapper {
   text-align: center;
   margin: 60px 0 50px 0;
}
@media screen and (min-width: 1366px) {
   .restaurant-page-wrapper .restaurant-page__button-wrapper {
      margin-top: 30px;
   }
}
.restaurant-page-wrapper .restaurant-page__button-wrapper__button {
   text-decoration: none;
   color: #fff;
   width: 70%;
   max-width: 215px;
   margin: auto;
   padding: 15px 20px;
   border-radius: 50px;
   background: linear-gradient(to left top, #9356dc 0%, #ff79da 100%);
   box-shadow: 1px 3px 6px rgba(50, 50, 50, 0.35);
   transition: 0.3s;
   background: linear-gradient(
      to left top,
      rgb(82, 30, 144) 0%,
      rgb(131, 0, 95) 100%
   );
}
.restaurant-page_new {
   font-weight: bold;
   position: absolute;
   top: 0;
   left: 50%;
   transform: translate(-50%, -50%);
   background: inherit;
   color: #ff79da;
   padding: 10px 20px;
   border-radius: 10px;
}
.restaurant-page-wrapper .restaurant-page__button-wrapper__button a {
   font-size: 1em;
}
.restaurant-page-wrapper .restaurant-page__button-wrapper__button:hover {
   opacity: 0.8;
   box-shadow: 1px 3px 6px rgba(50, 50, 50, 0.7);
}
.restaurant-page__plate:nth-child(1) {
   animation-name: fadein;
   animation-duration: 2s;
   animation-fill-mode: both;
   animation-delay: 0.2s;
}
.restaurant-page__plate:nth-child(2) {
