   animation-fill-mode: both;
   animation-delay: 0.6s;
}
.restaurant-page__plate:nth-child(4) {
   animation-name: fadein;
   animation-duration: 2s;
   animation-fill-mode: both;
   animation-delay: 0.8s;
} .blur {
